import React from 'react';

const mapToString = map => {
    let s = [];
    map.forEach((v, k) => {
        s.push(`"${k}" => "${v}"`)
    });
    return `Map (${map.size}) {${s.join(', ')}}`
};

export const InterpContext = React.createContext({
    name: '<uninitialized>', // Display name
    mode: '', // CodeMirror editor mode
    autoCloseBrackets: '', // CodeMirror auto-closing brackets
    extension: '.txt', // File extension
    setFilemap: filemap => console.log(`Unable to call 'interp.setFilemap(${mapToString(filemap)})' - context not initialized`), // Set the filesystem
    eval: str => console.log(`Unable to call 'interp.eval("${str}")' - context not initialized`), // Evaluate a string
    reset: () => console.log(`Unable to call 'interp.reset()' - context not initialized`) // Reset the evaluation context
});

